'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useSignIn, useUser } from '@clerk/nextjs';
import Logo from '@/components/Logo';
import { Lock, Mail, Eye, EyeOff, AlertCircle } from 'lucide-react';
import { checkUserExists, generateToken, storeTokenInCookies, getUserProfile } from '@/api/token-user-api';

export default function LoginPage() {
  const router = useRouter();
  const { signIn, isLoaded, setActive } = useSignIn();
  const { user: clerkUser } = useUser();
  
  const [formData, setFormData] = useState({
    email: '',
    password: '',
  });
  const [showPassword, setShowPassword] = useState(false);
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  // Redirect if already logged in
  useEffect(() => {
    if (isLoaded && clerkUser) {
      router.push('/dashboard');
    }
  }, [isLoaded, clerkUser, router]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    if (!isLoaded) {
      setError('Authentication service is loading...');
      setLoading(false);
      return;
    }

    try {
      // Sign in with Clerk
      const result = await signIn.create({
        identifier: formData.email,
        password: formData.password,
      });

      console.log('Sign-in result:', result);
      console.log('Sign-in status:', result.status);

      if (result.status === 'complete') {
        // Set the session as active
        if (setActive) {
          await setActive({ session: result.createdSessionId });
        }
        
        // After setting active, the user should be available in the global Clerk object
        // Wait a bit for Clerk to update
        await new Promise(resolve => setTimeout(resolve, 1000));
        
        // Try to get user ID from multiple sources
        let clerkUserId = clerkUser?.id;
        
        // If still not available, try from window.Clerk
        if (!clerkUserId && typeof window !== 'undefined' && (window as any).Clerk) {
          clerkUserId = (window as any).Clerk.user?.id;
        }
        
        console.log('Clerk User ID:', clerkUserId);
        console.log('Clerk User Object:', clerkUser);
        
        if (!clerkUserId) {
          // If we still don't have the user ID, redirect to dashboard
          // The page will handle authentication check
          console.log('Redirecting to dashboard - user ID will be available there');
          router.push('/dashboard');
          return;
        }

        // Step 1: Check if user exists in database
        const userExistsResponse = await checkUserExists(clerkUserId);
        
        if (!userExistsResponse.success || !userExistsResponse.data?.exists) {
          setError('User not found in database. Please contact support.');
          setLoading(false);
          return;
        }

        // Step 2: Generate authentication token
        const tokenResponse = await generateToken(clerkUserId);
        
        if (!tokenResponse.success || !tokenResponse.data?.token) {
          throw new Error(tokenResponse.message || 'Failed to generate authentication token');
        }

        const { token, user } = tokenResponse.data;

        // Step 3: Store token in cookies
        storeTokenInCookies(token);

        // Step 4: Get full user profile to verify status
        const profileResponse = await getUserProfile(token);
        
        if (!profileResponse.success || !profileResponse.data) {
          throw new Error('Failed to retrieve user profile');
        }

        const userProfile = profileResponse.data;

        // Step 5: Verify user is MASTER_NODE
        if (userProfile.role !== 'MASTER_NODE') {
          setError('Access denied. This portal is only for Master Node users.');
          setLoading(false);
          return;
        }

        // Step 6: Check if user status is active
        if (userProfile.status === 'PENDING_VERIFICATION') {
          setError('Your account is pending verification. Please contact support.');
          setLoading(false);
          return;
        }

        if (userProfile.status === 'SUSPENDED' || userProfile.status === 'BLOCKED') {
          setError('Your account has been suspended. Please contact support.');
          setLoading(false);
          return;
        }

        // Step 7: Redirect to dashboard
        router.push('/dashboard');
      } else {
        console.error('Unexpected sign-in status:', result.status);
        setError(`Login status: ${result.status}. Please try again or contact support.`);
      }
    } catch (err: any) {
      console.error('Login error:', err);
      setError(err.message || 'Invalid email or password. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setFormData(prev => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
    setError('');
  };

  return (
    <div className="min-h-screen bg-linear-to-br from-blue-50 via-white to-purple-50 flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        {/* Logo and Header */}
        <div className="text-center mb-8">
          <Logo size="lg" />
          <h1 className="text-3xl font-bold text-gray-900 mt-6 mb-2">Master Node Login</h1>
          <p className="text-gray-600">Access your master node dashboard</p>
        </div>

        {/* Login Card */}
        <div className="bg-white rounded-2xl shadow-xl p-8 border border-gray-100">
          <form onSubmit={handleSubmit} className="space-y-6">
            {/* Error Message */}
            {error && (
              <div className="bg-red-50 border border-red-200 rounded-lg p-4 flex items-start gap-3">
                <AlertCircle className="w-5 h-5 text-red-600 shrink-0 mt-0.5" />
                <p className="text-sm text-red-800">{error}</p>
              </div>
            )}

            {/* Email Field */}
            <div>
              <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-2">
                Email Address
              </label>
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Mail className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  id="email"
                  name="email"
                  type="email"
                  autoComplete="email"
                  required
                  value={formData.email}
                  onChange={handleChange}
                  className="block w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
                  placeholder="master@auramx.com"
                />
              </div>
            </div>

            {/* Password Field */}
            <div>
              <label htmlFor="password" className="block text-sm font-medium text-gray-700 mb-2">
                Password
              </label>
              <div className="relative">
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <Lock className="h-5 w-5 text-gray-400" />
                </div>
                <input
                  id="password"
                  name="password"
                  type={showPassword ? 'text' : 'password'}
                  autoComplete="current-password"
                  required
                  value={formData.password}
                  onChange={handleChange}
                  className="block w-full pl-10 pr-12 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all"
                  placeholder="••••••••"
                />
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  className="absolute inset-y-0 right-0 pr-3 flex items-center"
                >
                  {showPassword ? (
                    <EyeOff className="h-5 w-5 text-gray-400 hover:text-gray-600" />
                  ) : (
                    <Eye className="h-5 w-5 text-gray-400 hover:text-gray-600" />
                  )}
                </button>
              </div>
            </div>

            {/* Submit Button */}
            <button
              type="submit"
              disabled={loading}
              className="w-full bg-linear-to-r from-blue-600 to-purple-600 text-white py-3 px-4 rounded-lg font-semibold hover:from-blue-700 hover:to-purple-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 transition-all disabled:opacity-50 disabled:cursor-not-allowed"
            >
              {loading ? (
                <span className="flex items-center justify-center gap-2">
                  <svg className="animate-spin h-5 w-5" viewBox="0 0 24 24">
                    <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4" fill="none" />
                    <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z" />
                  </svg>
                  Signing in...
                </span>
              ) : (
                'Sign In'
              )}
            </button>
          </form>

          {/* Divider */}
          <div className="mt-6 relative">
            <div className="absolute inset-0 flex items-center">
              <div className="w-full border-t border-gray-200"></div>
            </div>
            <div className="relative flex justify-center text-sm">
              <span className="px-4 bg-white text-gray-500">New to AuramX?</span>
            </div>
          </div>

         
        
        </div>

        {/* Footer */}
        <p className="text-center text-sm text-gray-500 mt-8">
          Secure Master Node Portal • AuramX Investment Platform
        </p>
      </div>
    </div>
  );
}
